  position: relative;
  vertical-align: middle;
  display: inline-block;
  background-color: #21D380;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  margin-right: 10px;
}

.ico-circlegreencheck:after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 14px;
  height: 14px;
  background: url(/images/ico/ico-checkwhite.svg);
